const main = {
    init() {
        $('.js-select').nSelect({
            afterOpen: function() {
                $('.nselect').on('click', function () {
                    $('.nselect').not(this).each(function () {
                        $(this).removeClass('_active');
                    });
                });
            }
        });


        let menuLink = $('.header__menu-logo');
        let menu = $('.header__menu');

        menuLink.on('click', function () {
            menu.toggleClass('header__menu--active');
            menuLink.toggleClass('header__menu-logo--active');
        });
    }
};

export default main;



